import ArrowDownIcon from 'vue-material-design-icons/ArrowDown.vue';
import creation from '@mdi/svg/svg/creation.svg';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import NcChip from '@nextcloud/vue/dist/Components/NcChip.js';
import LoadingSkeleton from './LoadingSkeleton.vue';
export default {
  name: 'ThreadSummary',
  components: {
    LoadingSkeleton,
    NcButton,
    NcChip,
    ArrowDownIcon
  },
  props: {
    summary: {
      type: String,
      required: true
    },
    loading: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      creation
    };
  },
  computed: {
    brand() {
      if (OCA.Theming) {
        return t('mail', '{name} Assistant', {
          name: OCA.Theming.name
        });
      }
      return t('mail', '{name} Assistant', {
        name: 'Nextcloud'
      });
    }
  },
  methods: {
    onScroll() {
      let container = document.querySelector('.splitpanes__pane-details');
      if (!container) {
        container = document.querySelector('.app-content-wrapper--mobile');
      }
      container.scrollTo({
        top: container.scrollHeight,
        left: 0,
        behavior: 'smooth'
      });
    }
  }
};