import NcAvatar from '@nextcloud/vue/dist/Components/NcAvatar.js';
import { fetchAvatarUrlMemoized } from '../service/AvatarService.js';
import logger from '../logger.js';
export default {
  name: 'Avatar',
  components: {
    NcAvatar
  },
  props: {
    displayName: {
      type: String,
      required: true
    },
    email: {
      type: String,
      required: true
    },
    disableTooltip: {
      type: Boolean,
      default: false
    },
    size: {
      type: Number,
      default: 40
    }
  },
  data() {
    return {
      loading: true,
      avatarUrl: undefined
    };
  },
  computed: {
    hasAvatar() {
      return this.avatarUrl !== undefined;
    }
  },
  async mounted() {
    if (this.email !== '') {
      try {
        this.avatarUrl = await fetchAvatarUrlMemoized(this.email);
      } catch (_unused) {
        logger.debug('Could not fetch avatar', {
          email: this.email
        });
      }
    }
    this.loading = false;
  }
};