import { NcLoadingIcon as IconLoading, NcEmptyContent as EmptyContent } from '@nextcloud/vue';
export default {
  name: 'Loading',
  components: {
    IconLoading,
    EmptyContent
  },
  props: {
    hint: {
      type: String,
      default: '',
      required: false
    },
    slowHint: {
      type: String,
      default: '',
      required: false
    }
  },
  data() {
    return {
      slow: false,
      slowTimer: undefined
    };
  },
  mounted() {
    clearTimeout(this.slowTimer);
    this.slowTimer = setTimeout(() => {
      this.slow = true;
    }, 3500);
  },
  beforeDestroy() {
    clearTimeout(this.slowTimer);
  }
};