  width: 100%;
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 10px 0;
}

/* show icon + text for Download all button
		as well as when there is only one attachment */
.attachments-button-wrapper {
  text-align: center;
  display: flex;
  align-items: center;
}
.show-more-attachments {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 2px 0;
  color: var(--color-text-lighter);
}
.show-more-attachments span {
  cursor: pointer;
}
.show-more-attachments:hover {
  color: var(--color-main-text);
}
@keyframes spin {
0% {
    transform: rotate(0deg);
}
50% {
    transform: rotate(180deg);
}
100% {
    transform: rotate(360deg);
}
}
.spin {
  animation: spin 1s linear infinite;
}
.attachment-link {
  cursor: pointer;
  display: flex;
  align-items: center;
  color: var(--color-text-lighter);
}
.attachment-link:hover {
  color: var(--color-main-text);
}
.attachment-link span {
  margin: 0 4px 0 16px;
}
.oc-dialog {
  z-index: 10000000;
}
.mail-message-attachments {
  display: flex;
  flex-wrap: wrap;
  padding: 10px 6px 10px 46px;
  margin-top: 4px;
  margin-bottom: 0;
  position: sticky;
  bottom: 0;
  background: linear-gradient(0deg, var(--color-main-background), var(--color-main-background) 90%, rgba(255, 255, 255, 0));
}
.mail-message-attachments--wrapper {
  display: flex;
  width: 100%;
  height: auto;
  overflow: hidden;
  max-height: none;
}
.mail-message-attachments--wrapper.hide {
  display: flex;
  max-height: 70px;
