import { NcButton, NcModal } from '@nextcloud/vue';
import { translate as t } from '@nextcloud/l10n';
export default {
  name: 'ConfirmationModal',
  components: {
    NcButton,
    NcModal
  },
  props: {
    title: {
      type: String,
      required: true
    },
    confirmText: {
      type: String,
      default: t('mail', 'Confirm')
    },
    confirmUrl: {
      type: String,
      default: undefined
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    confirm() {
      this.$emit('confirm');
    },
    cancel() {
      this.$emit('cancel');
    }
  }
};